<template>
  <!-- <router-link tag="div" class="mask" to="home">
    <img class="share" src="../assets/share.png" alt="">
  </router-link> -->
  <div class="mask" @click="getRoute()">
    <img class="share" src="../assets/share.png" alt="">
  </div>
</template>

<script>
export default {
  name: 'isShare',
  methods: {
    getRoute() {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.share {
  float: right;
  padding: 5%;
  width: 40%;
}
</style>


